import React from 'react';
import Card from '@components/Card';
import Utterances from '@components/Utterances';
import { UTTERANCES_THEME } from '@constants';
import { DeckdeckgoHighlightCodeCarbonTheme } from '@deckdeckgo/highlight-code';

import { PostLayout, PostFileContent, PostMarkdownContent } from './Styles';

interface Props {
  ext: string;
  content: string;
  html?: string;
  utterances?: string;
}

function PostPresenter({ ext, content, html, utterances }: Props) {
  return (
    <PostLayout>
      <Card>
        <PostFileContent>
          <deckgo-highlight-code
            language={ext.replaceAll('.', '')}
            theme={DeckdeckgoHighlightCodeCarbonTheme.DRACULA}
          >
            <code slot="code">{content}</code>
          </deckgo-highlight-code>
        </PostFileContent>
      </Card>

      {html && (
        <Card overflowHidden={false}>
          <PostMarkdownContent dangerouslySetInnerHTML={{ __html: html }} />
        </Card>
      )}

      {utterances && (
        <Card>
          <Utterances repo={utterances} theme={UTTERANCES_THEME.GITHUB_LIGHT} />
        </Card>
      )}
    </PostLayout>
  );
}

export default PostPresenter;
