import Post from '@components/Pages/Post';
import { graphql } from 'gatsby';

export default Post;

export const query = graphql`
  query ($slug: String!, $slugNoExt: String!) {
    file(relativePath: { eq: $slug }) {
      ext
      name
      fields {
        content
        stampObject {
          modified
          created
        }
      }
    }
    markdownRemark(fields: { slug: { eq: $slugNoExt } }) {
      html
      frontmatter {
        title
      }
    }
  }
`;
